
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import Scroll from '@/components/scroll'
import LoadStutusMixin from '@/js/mixin/loadStaus'
import BarChangeMixin from '@/js/mixin/barChange'
import LoadImg from '@/components/LoadImg'

export default {
name: "UserManager",
components: {
Cell,
Group,
Scroll,
LoadImg,
PopupPicker,
XInput,
XButton
},
data () {
return {
page:1,
showPopupPicker: false,
itemShowPopupPicker: false,
groupValue: [],
itemValue: [],
userId: '',
searchValue: '',
groups: [[
{
name: '普通用户',
value: '0'
},
{
name: 'VIP用户',
value: '1'
},
{
name: '马甲用户',
value: '2'
},
{
name: '管理员',
value: '3'
},
{
name: '免费霸屏用户(1次)',
value: '4'
},
{
name: '免费霸屏用户(2次)',
value: '5'
},
{
name: '免费霸屏用户(5次)',
value: '6'
},
{
name: '免费霸屏用户(10次)',
value: '7'
},
{
name: '免费霸屏用户(20次)',
value: '8'
}
]],
conditionGroups: [[
{
name: '全部',
value: '-1'
},
{
name: '普通用户',
value: '0'
},
{
name: 'VIP用户',
value: '1'
},
{
name: '马甲用户',
value: '2'
},
{
name: '管理员',
value: '3'
},
{
name: '免费霸屏用户(1次)',
value: '4'
},
{
name: '免费霸屏用户(2次)',
value: '5'
},
{
name: '免费霸屏用户(5次)',
value: '6'
},
{
name: '免费霸屏用户(10次)',
value: '7'
},
{
name: '免费霸屏用户(20次)',
value: '8'
}
]],
}

},
mixins: [LoadStutusMixin, BarChangeMixin],
methods: {
getUsers () {
this.onloadStart()
this.$api.getBarUsers(this.$barId,this.groupValue[0],this.page,)
.then(data => {
let list = []
for (let item of data) {
if (!(item.user.id + '').includes(this.searchValue) && !item.user.nickname.includes(this.searchValue)) {
continue
}
item.value = [item.group_id + '']
list.push(item)
}
if (this.page === 1) {
this.list = list
this.page++
} else if (list.length > 0) {

this.list = this.list.concat(list)
this.page++
} else {
this.$refs.scroll.forceUpdate()
}
this.onLoadSuccess()
})
.catch(err => {
this.onLoadError()
})
},
onBarChange () {
this.page = 1
this.getUsers()
},
onGroupChange (value) {
this.page = 1
this.list = []
this.getUsers()
},
search(){
this.page = 1
this.list = []
this.getUsers()
},
onChange (value) {
let data = {
group_id: ~~value
}
this.$api.changeUserGroup(this.$barId,this.userId,data)
.then(item => {
item.value = [item.group_id + '']
this.$util.change1(this.list,item)
})
},
onCellClick(item){

this.userId = item.user_id
this.itemShowPopupPicker=true
}
},
created () {
this.page = 1
this.getUsers()
}
}



